/* header
------------------------------------------------------ */
@use "../base/setting"as *;
@use "../global/mixin/breakpoint"as *;

.header {
  position: fixed;
  height: 64px;
  padding: 0 $padding-side;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include mq(md) {
    height: 86px;
    padding: 0 30px;
  }
}


.header-nav {
  visibility: hidden; //display:noneはtransitionが効かない
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: gray;
  opacity: 0;
  transition: opacity .5s;
  z-index: 2;
  @include mq(md) {
    visibility: visible;
    position: static;
    width: auto;
    height: auto;
    opacity: 1;
    background-color: transparent;
  }
}

.header-nav.fade {
opacity: 1;
visibility: visible;
}


.header-nav__list {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}