/* hamburger
------------------------------------------------------ */
@use "../base/setting"as *;
@use "../global/mixin/breakpoint"as *;

.hamburger {
  @include z-index(hamburger);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: aqua;
  @include mq(md) {
    display: none;
  }
}

.hamburger__line {
  position: relative;
  width: 25px;
  height: 2px;
  background: #fff;
}

// 上下の線、幅
.hamburger__line::before,
.hamburger__line::after {
  position: absolute;
  content: "";
  display: block;
  width: 25px;
  height: 2px;
  background: #fff;
  transition: transform 0.3s;
}

// 線の高さ
.hamburger__line::before {
  top: -8px;
}
.hamburger__line::after {
  bottom: -8px;
}

/*閉じる*/
.hamburger.close .hamburger__line{
  background: transparent;
}
.hamburger.close .hamburger__line::before {
  top: 0;
  transform: rotate(45deg);
}
.hamburger.close .hamburger__line::after {
  bottom: 0;
  transform: rotate(-45deg);
}