/*----------------------------------------
	共通部分
----------------------------------------*/
@use "setting"as *;
@use "../global/" as *;

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  line-height: 1.8;
  color: $c_text;
  letter-spacing: 0.05em;
  font-size: rem(15);

  @include mq(md) {
    font-size: rem(14);
  }
}

a {
  transition: 0.3s;
  text-decoration: none;
  color: $c_key1;
  &:hover {
    text-decoration: underline;
  }
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

li {
  list-style: none;
}