@use "../global/" as *;


// コンテナーの左右余白
$padding-side: 15px;
// $padding-side-sp: 15px;

// ページの全体の横幅（大）
$width_lg : 1300px;

// ページの全体の横幅（標準）
$width : 1140px;

// ページの全体の横幅（中間）
$width_md : 950px;

// ページの全体の横幅（狭い）
$width_sm : 780px;

// レイアウト幅
