//白黒
$c_white: #ffffff;
$c_black: #000000;

//グレー
$c_gray1: #f0f0f0;


//キーカラー
$c_key1: #391e88;
$c_key2: #21054d;
$c_key3: #393636;


// 標準テキストカラー
$c_text: #333;


// ボタンのグラデーションカラー
$btn-gradient: linear-gradient(to right, #7633a4 0%, #250d65 51%, #7633a4 100%);

// リンクカラー
// $c_link: $c_text;
// $c_hover: $c_key3;
// $c_active: $c_key3;

// z-index
$layer: (
  hamburger: 100,
  header: 20,
  tooltip: 10,
  badge: 2,
  default: 1,
);

@mixin z-index($key) {
  z-index: map-get($layer, $key);
}